* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto";
  color: #121212;
  font-style: normal;
}

.container {
  max-width: 1440px;
}

.row {
  max-width: 980px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  padding-top: 6rem;
}

.w-50 {
  width: 50%;
}

.img-box img {
  max-width: 100%;
}

.text-box {
  padding-left: 5.9375rem;
}
.text-box h1 {
  font-weight: 700;
  font-size: 2.625rem;
  line-height: 3.125rem;
  padding-top: 0.625rem;
}
.text-box p {
  font-weight: 300;
  font-size: 1rem;
  line-height: 1.5rem;
  letter-spacing: 0.0187rem;
}
.text-box .btn-open-modal {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 1rem 2.5rem;
  background: #E4C1AE;
  border-radius: 2rem;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1rem;
  color: #FFFFFF;
  border: none;
  margin-top: 1.875rem;
}
.text-box .btn-open-modal:hover, .text-box .btn-open-modal:focus {
  cursor: pointer;
}

.modal-box {
  /* Modal background */
  display: none;
  /* Hidden by default */
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
}
.modal-box .modal-content {
  position: absolute;
  width: 100%;
  max-width: 920px;
  height: 230px;
  left: 255px;
  top: 380px;
  display: flex;
  flex-wrap: wrap;
  background-color: #fff;
}
.modal-box .btn-close-modal {
  float: right;
  font-size: 1.75rem;
  font-weight: 600;
  padding-top: 2.3rem;
  padding-right: 2.875rem;
}
.modal-box .btn-close-modal:hover, .modal-box .btn-close-modal:focus {
  cursor: pointer;
}
.modal-box .counter #btn-clear {
  padding: 1rem 2.5rem;
  background: #E4C1AE;
  border-radius: 2rem;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1rem;
  color: #FFFFFF;
  border: none;
  display: none;
}
.modal-box .counter #btn-clear:hover, .modal-box .counter #btn-clear:focus {
  cursor: pointer;
}

@media only screen and (max-width: 1200px) {
  .modal-box .modal-content {
    left: 200px;
    width: 70%;
    height: 260px;
  }
}
@media only screen and (max-width: 992px) {
  .text-box {
    padding-left: 4rem;
  }
  .text-box h1 {
    padding: 0;
    line-height: 1.2rem;
  }

  .row {
    padding-left: 0.9375rem;
    padding-right: 0.9375rem;
  }

  .modal-box .modal-content {
    left: 100px;
    width: 80%;
  }
}
@media only screen and (max-width: 768px) {
  .modal-box .modal-content {
    left: 25px;
    width: 90%;
    height: 230px;
    top: 500px;
  }

  .w-50 {
    width: 100%;
  }

  .w-80 {
    width: 80%;
  }

  .w-20 {
    width: 20%;
  }

  .text-box {
    padding-left: 1.25rem;
    padding-top: 1.875rem;
  }
}